@import './styles/utils.scss';

.profile-event-card {
  max-width: 270px;
  width: 100%;
  height: 158px;
  flex-shrink: 0;
  margin-right: 30px;
  padding: 30px;

  &:hover {
    cursor: pointer;
  }

  &:last-of-type {
    margin-right: 0;
  }

  &__date {
    display: flex;
    margin-bottom: 10px;
  }

  &__day {
    margin-right: 10px;
    font-family: $protoGrotesk;
    font-style: normal;
    font-weight: 300;
    font-size: 44px;
    line-height: 1.13;  
  }

  &__month {
    font-family: $ptRootUI;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.36;
    letter-spacing: 0.2px;
  }

  &__title {
    margin: 0;
    font-family: $ptRootUI;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.36;
    letter-spacing: 0.2px;
    color: $black;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -moz-box;
    -moz-box-orient: vertical;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-clamp: 2;
    box-orient: vertical;
  }
}

@include atTablet {
  .profile-event-card {
    max-width: 205px;
    max-height: 108px;
    padding: 15px;

    &:first-of-type {
      margin-left: 60px;
    }

    &:last-of-type {
      margin-right: 15px;
    }

    &__day {
      font-size: 37px;
      line-height: 1.05;
    }

    &__month {
      font-size: 12px;
      line-height: 1.2;
    }

    &__title {
      font-size: 12px;
      line-height: 1.2;
    }
  }
}

@include atMobile {
  .profile-event-card {
    &:first-of-type {
      margin-left: 15px;
    }
  }
}
